import { DatePicker, InputNumber, Menu, Select } from "antd";
import dayjs from "dayjs";
import { useState } from "react";
import "./AppHeader.css";
import moment from "moment";
import { useLocation } from "react-router-dom";
const { Option } = Select;

const { RangePicker } = DatePicker;
const status = [0, 1, 2, 3, 4];

const AppHeader = ({
  wbKeys = [],
  currentWbKey,
  setCurrentWbKey,
  date,
  setDate,
}) => {
  const dateFormat = "DD-MM-YYYY";
  let router = useLocation();
  const [days, setDays] = useState();

  const hideElements = () => {
    return !["/goodlist", "/"].includes(router.pathname);
  };

  return (
    <div className={"wrapper"}>
      <div className="box">
        <div>{` : ${dayjs(new Date()).format(
          "DD.MM.YY HH.mm"
        )}`}</div>
        <div> </div>
      </div>
      <div className="box">
        <RangePicker
          defaultValue={[
            date ? moment(date[0], dateFormat) : null,
            date ? moment(date[1], dateFormat) : null,
          ]}
          format={dateFormat}
          onChange={setDate}
          placeholder={[" ", " "]}
        />
        {hideElements() && (
          <div className="title">
              :{" "}
            <InputNumber min={1} value={days} onChange={setDays} />
          </div>
        )}
      </div>
      {hideElements() && (
        <div className="box">
          <div> 20%</div>
        </div>
      )}
      <div className="box">
        <div>  </div>
        <div className="progressWrapper">
          {status.map((item, i) => (
            <div key={i} className="progress"></div>
          ))}
        </div>
      </div>
      <div className="box">
        <Select
          mode="multiple"
          showArrow
          defaultValue={currentWbKey}
          placeholder="wbKeys"
          style={{
            width: 300,
          }}
          onChange={setCurrentWbKey}
        >
          {wbKeys.map((item) => (
            <Option key={item.id} value={item.id}>
              {item.name}
            </Option>
          ))}
        </Select>
      </div>
    </div>
  );
};

export default AppHeader;
